@import url("https://fonts.googleapis.com/css?family=Lato|Tienne");
html,
body {
  margin: 0;
  padding: 0;
}

body {
  overflow-x: hidden;
  background-color: #efefef;
  margin-bottom: 25px;
}

#image404 {
  width: 110%;
  margin-left: -5%;
}

.logo a img {
  height: 35px;
  padding-top: 17px;
}

.menu span.nav {
  width: 25px;
  height: 5px;
  background-color: #2d374b;
  padding: 5px, 0;
}

.header {
  background-color: none;
}

.containerMenuButton {
  display: inline-block;
  cursor: pointer;
  padding: 15px 0 0 10px;
}

.bar1,
.bar2,
.bar3 {
  width: 30px;
  height: 5px;
  background-color: #2d374b;
  margin: 6px 0;
  transition: 0.4s;
  border-radius: 2px;
}

.bar3 {
  width: 17px;
}

.change .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px, 6px);
  transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  -webkit-transform: rotate(-135deg) scaleX(1.75) translateY(11px) translateX(3px);
  transform: rotate(-135deg) scaleX(1.75) translateY(11px) translateX(3px);
}

.change #menu {
  display: none;
}

.change #page {
  display: block;
}

#menu {
  display: none;
}

#page {
  display: block;
}

#menuTittle {
  line-height: 1rem;
}

.radius5 {
  border-radius: 5px;
}

.noPadding {
  padding: 0;
}

.menuItem {
  margin-top: -5%;
}

.fb-grid__container {
  background-image: url("https://image.ibb.co/ge8PKw/background.png");
  background-repeat: no-repeat;
  background-size: 100%;
}

.picText {
  background-color: white;
  padding: 0;
  border-radius: 15px;
}

.picText img {
  width: 100%;
  padding: 0;
  border-radius: 5% 5% 0 0;
}

.scrolling {
  overflow: auto;
  white-space: nowrap;
}

.schrink {
  width: 100%;
}

.scroll {
  display: inline-block;
  text-align: center;
  padding: 0;
  text-decoration: none;
  width: 100%;
}

#page > div.fb-grid__row.scrolling.scroll > div:nth-child(2) {
  padding: 0;
}

.blogSum img {
  width: 100%;
}

.menuItem {
  margin: 0.5rem;
}

.footer img {
  width: 50%;
}

.height {
  height: 250px;
  object-fit: cover;
}

.textHeight {
  height: 150px;
  overflow: hidden;
}

.input {
  width: 270px;
  height: 25px;
  border: none;
}

.inputMessage {
  width: 270px;
  height: 150px;
  border: none;
}

.submit {
  background-color: #97cfde;
