  margin-top: 15px;
  padding-top: 0;
  display: block;
  margin-left: 5%;
}

.tag {
  background-color: #26a69a;
  padding: 5px 10%;
  margin: 15px 5px;
  border-radius: 5px;
  display: block;
  width: fit-content;
}

.tag.Ionic,
.tag.JavaScript,
.tag.CSS {
  background-color: #97cfde;
  color: #2d374b;
}

.tag.Undifined {
  display: none;
}

.disabled {
  background-color: #efefef;
  color: #9b9b9b;
}

.active {
  border-color: #26a69a;
  color: #2d374b;
}

.following {
  background-color: #26a69a;
  color: #2d374b;
}

